
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 1rem;
  padding: 0;
}
fieldset {
  margin: 0 0 1rem;
}
label,
input {
  display: block;
}
label {
  margin: 0 0 1rem;
}
.field {
  -webkit-transition: background-color 1.5s;
  transition: background-color 1.5s;
}
.field--error {
  background-color: Pink;
  -webkit-transition: none;
  transition: none;
}
.table {
  border: 1px solid #999;
}
.table__row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  border-bottom: 1px solid #999;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}
.table__row--inserted {
  -webkit-transition: background-color 1.5s;
  transition: background-color 1.5s;
}
.table__row--highlighted {
  background-color: LightSteelBlue;
  -webkit-transition: none;
  transition: none;
}
.table__body .table__row:nth-child(even) {
  background: rgba(0, 0, 0, 0.1);
}
.table__body .table__row:hover {
  background: rgba(0, 0, 0, 0.15);
}
.table__head {
  background: #999;
  -webkit-box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.2);
          box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.2);
}
.table__body {
  height: 250px;
  overflow-y: auto;
  overflow-x: hidden;
}
.table__foot {
  background: #999;
  -webkit-box-shadow: 0 -0.2rem 0.5rem rgba(0, 0, 0, 0.2);
          box-shadow: 0 -0.2rem 0.5rem rgba(0, 0, 0, 0.2);
}
.table__cell--head {
  font-weight: bold;
}
.table__cell {
  width: 50%;
  padding: 0.5rem 1rem;
}
.table__cell--numeric {
  text-align: right;
}
.table__cell--colspan-2 {
  width: 100%;
}
.table__foot {
  text-align: right;
}
.action {
  display: inline-block;
  padding: 0 0.2rem;
  font-weight: bold;
  cursor: pointer;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}
.action::-moz-selection {
  background: none;
}
.action::selection {
  background: none;
}
.action--edit {
  font-size: 0.9rem;
  font-weight: normal;
  color: blue;
}
.action--remove {
  color: red;
}
.action--remove:hover {
  color: darkred;
}
[class*="grid__col--"] {
  margin-right: 1rem;
}
@media (min-width: 625px) {
.grid {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}
.grid__col--3 {
    width: 25%;
}
.grid__col--6 {
    width: 50%;
}
.grid__col--9 {
    width: 75%;
}
[class*="grid__col--"]:last-child,
  [class*="grid__col--"]:only-child {
    margin-right: 0;
}
}



